.meal {
    width: 375rem;
    height:calc(100vh - 40rem);
    padding-top: 40rem;
    overflow-y: scroll;
    padding-bottom: 60rem;
    .mealItem {
        width: 375rem;
        height: 100rem;
        font-size: 50rem;
        display: flex;
        padding-top: 10rem;
        border-bottom: 1rem solid rgba(0,0,0,.1);
        .left {
            width: 30%;
            height: 100rem;

            img {
                width: 100%;
                height: 100rem;
            }
        }

        .right {
            width: 70%;
            height: 100rem;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding-left: 10rem;

            .text {
                p:nth-child(1) {
                    color: #666464;
                    font-size: 14rem;
                }

                p:nth-child(2) {
                    color: #cecece;
                    font-size: 14rem;
                }
            }

            .priceBox {
                display: flex;
                justify-content: space-between;

                .price {
                    width: 40rem;
                    height: 40rem;
                    line-height: 40rem;
                }

                .btnBox {
                    display: flex;
                    justify-content: right;
                    align-items: center;
                    flex: 1;
                    padding-right: 12rem;
                    text-align: center;
                    line-height: 15rem;

                    p {
                        color: #000;
                        font-size: 12rem;
                        margin: 0rem 5rem 0rem 5rem;
                        padding:1rem 1rem -2rem 1rem;
                    }

                    .jianItem {
                        width: 15rem;
                        height: 15rem;
                        background-color: #edb000;
                        border-radius: 50%;
                        color: white;

                    }

                    .addItem {
                        width: 15rem;
                        height: 15rem;
                        background-color: #edb000;
                        border-radius: 50%;
                        color: white;
                    }
                }
            }
        }
    }
}

.meal::-webkit-scrollbar {
    display: none;
  }

 