.car{
    width:340rem;
    height:40rem;
    border: 1rem solid #000;
    position: fixed;
    bottom: 10rem;
    left: 10rem;
    background-color:rgba(0,0,0,.8);
    border-radius: 20rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 10rem;
    position: fixed;
    z-index: 1000;
    .dot{
        position: absolute;
        top: -11rem;
        left:0rem;
        background-color: red;
        color: white;
        width: 20rem;
        height: 20rem;
        text-align: center;
        line-height: 20rem;
        border-radius: 50%;
        font-size: 12rem;

    }
    .price{
        color: white;
    }
    .btn{
        border:0;
        width:80rem;
        height:42rem;
        border-radius: 20rem;
        background-color: #f8bc02;
        margin: -1rem -2rem -1rem -1rem;
        font-size: 14rem;
    }
    .btn01{
        border:0;
        width:80rem;
        height:42rem;
        border-radius: 20rem;
        background-color: #6b6b6b;
        margin: -1rem -2rem -1rem -1rem;
        font-size: 14rem;
    }
}