.carlist{
    width:100%;
    height:60%;
    background-color: white;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 20rem;
    overflow-y: scroll;
   
    .header{
        width:100%;
        height:50rem;
        border-radius: 20rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 14rem;
        padding: 0rem -7rem 0rem 7rem;
        position: fixed;
        background-color: white;
        z-index: 100;
        div,h1{
         transform: scale(.8);
        }
       
    }
    .cardetail{
        width:100%;
        height:calc(70vh - 106rem);
        margin-bottom: 106rem;
    }
}
.carlist::-webkit-scrollbar {
    display: none;
  }

 